body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
#contact{ 
  text-align: center;
}
#portfolio{
  display:flex;
}
.portfolio h2 { 
  text-align: center
}
#resume{
  text-align: center
}
#resume h2{
  text-align: center
}
#projects h2{
  color:#374054;margin-bottom:50px
}
.project{
  position:relative;
  max-width:900px;
  margin:0 auto 30px auto;
  overflow:hidden;
  background:#fff;
  border-radius:4px
}
.project-image{
  float:left
}
.project-info{
  position:absolute;
  top:50%;
  transform:translateY(-50%);
  margin-left:300px;
  padding:15px
}
.project-info h3{
  font-size:1.5em;
  font-weight:300;
  color:#374054;
  margin:0 0 15px 0
}
.project-info p{
  color:#74808a;
  margin:0 0 15px 0;
  font-size:0.9em
}
.no-image .project-info{
  position:relative;
  margin:0;
  padding:30px 15px;
  transform:none
}
#more-projects{
  display:none
}
#skills{
  padding:50px 15px;
  text-align:center
}
